

#about-me {
    color: white;
    margin-bottom: 450px;
}

.cv-link{
    text-decoration: underline !important;
}

.details {
    color: white;
    background: rgba(0, 82, 212, 0.18); /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, rgba(111, 177, 252, 0.18), rgba(67, 100, 247, 0.18), rgba(0, 82, 212, 0.19)); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, rgba(111, 177, 252, 0.18), rgba(67, 100, 247, 0.18), rgba(0, 82, 255, 0.18));
    border-radius: 25px;
    box-shadow: 0px 0px 15px #0052D4;
    padding: 25px;
    margin-top: 150px;
}

.aboutMePicture {
    box-shadow: 15px 15px 15px #0052D4;
    background-image: url("../img/IMG_4540.jpg") !important;
    background-size: cover !important;
    min-width: 100px;
    min-height: 600px;
    border-radius: 25px;
    margin-top: 130px;

}

