.landing-screen{
    padding-bottom: 200px;
}
.fa-at,.fa-linkedin,.fa-stack-overflow,.fa-github{
    color:white;
    opacity: .8;
}
@-ms-viewport{
    width: 500px;
}
.fa-at:hover{
    color:white;
    opacity: .5;
}
.fa-linkedin:hover{
    color:#00a0dc;
}
.fa-stack-overflow:hover{
    color:#f48024;
}
.fa-github:hover{
    color:#333;
}
.position{
    justify-content: center;
}
.avatar{
    border-radius: 50%;
    height: 20%;
    width: 20%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 10px 10px 20px #0052D4;
}
.cursor{
    color: white !important;
}
.info-box-cell{
    text-align: center;
    color: white;
    background: rgba(0, 82, 212, 0.18);  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, rgba(111, 177, 252, 0.18), rgba(67, 100, 247, 0.18), rgba(0, 82, 212, 0.19));  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, rgba(111, 177, 252, 0.18), rgba(67, 100, 247, 0.18), rgba(0, 82, 255, 0.18));
    border-radius: 25px;
    box-shadow: 7px 10px 20px #0052D4;
    margin: auto;
    position: relative;
}
.icons-social-media{
    padding: 30px;
}
