@media screen and (max-width: 600px) {
    .aboutMePicture {
        display: none !important;
    }

    .details {
        display: inline-block;
        padding: 25% !important;
        margin-left: 95px;
        margin-top: 100px;
    }

    .info-box-cell {
        padding-right: 100px;
        padding-left: 100px;
    }

    .card {
        height: 100%;
        width: 100%;
    }
    .card-mobile{
        word-wrap: break-word;
    }
}


body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #0052D4; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #6FB1FC, #4364F7, #0052D4); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #6FB1FC, #4364F7, #0052D4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    color: white;
    text-decoration: none;
}

h4, a, .info-box-cell, p {
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}

h2 {
    color: white;
}

html {
    scroll-behavior: smooth;
}

